.form-login {
  position: relative;
  z-index: 3;

  display: flex;
  flex-direction: column;

  &__header {
    margin-bottom: 22px;

    & h3 {
      margin-bottom: 22px;
      font-family: $mainFontBold;
      font-style: normal;
      font-weight: $mainFontWeightBold;
      font-size: get-vw(52px);
      line-height: get-vw(63px);
      text-align: center;
      letter-spacing: 0.472727px;
      color: #000000;

      @media (max-width: $table) {
        font-size: get-vw(70px);
      }
    }

    & p {
      font-style: normal;
      font-weight: normal;
      font-size: get-vw(20px);
      line-height: get-vw(24px);
      text-align: center;
      letter-spacing: 0.4px;

      color: #808080;

      @media (max-width: $table) {
        font-size: get-vw(50px);
      }
    }
  }

  &__footer {
    display: flex;
    flex-direction: column;

    & p {
      margin-bottom: 22px;
      text-align: center;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 16px;
      letter-spacing: 0.531818px;

      color: #8b8b8b;

      @media (max-width: $table) {
        font-size: get-vw(50px);
      }
    }

    & a {
      display: flex;
      align-items: center;
      margin-bottom: 22px;
      padding: get-vw(14px) get-vw(40px);

      font-style: normal;
      font-weight: normal;
      font-size: 14px;
      line-height: 17px;

      letter-spacing: 1px;
      color: #ffffff;
      background: linear-gradient(270deg, #67e4fe 0%, #35abd3 52.4%);
      border-radius: 27.5px;
      text-decoration: none;
      transition: $transition-base;

      @media (max-width: $table) {
        padding: 1em;
      }

      &:focus,
      &:hover {
        opacity: 0.5;
        text-decoration: none;
      }
    }

    & svg {
      margin-right: 20px;
    }
  }

  & label {
    padding-bottom: 22px;
  }

  & input {
    display: inline-block;
    vertical-align: middle;
    padding: get-vw(19px) get-vw(45px);
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;

    letter-spacing: 0.315151px;

    color: #a0a0a0;

    width: 100%;

    background: #f0f0f0;
    border: 1px solid #ffffff;
    box-sizing: border-box;
    border-radius: 42.5px;
    outline: none;

    cursor: pointer;
    transition: $transition-base;
    outline: none;

    @media (max-width: $table) {
      padding: 1em;
    }

    &:focus,
    &:hover {
      opacity: 0.5;
      outline: none;
    }
  }

  & button[type="submit"] {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 22px;
    padding: get-vw(19px);
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;

    text-align: center;
    letter-spacing: 4.2px;

    color: #ffffff;
    background: linear-gradient(90deg, #6ed4ff 34.25%, #8fffa3 96.88%);
    opacity: 0.79;
    border: none;
    border-radius: 27.5px;
    cursor: pointer;
    transition: $transition-base;
    outline: none;
    text-transform: uppercase;

    @media (max-width: $table) {
      padding: 1em;
    }

    &:focus,
    &:hover {
      opacity: 0.5;
      outline: none;
    }

    & svg {
      position: absolute;
      top: 50%;
      right: 15px;
      transform: translateY(-50%);
    }
  }
}
