.login {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  height: 100%;
  width: 100%;

  background-color: transparent;
  background-size: cover;

  background: url("../img/login/Box.png"),
    linear-gradient(360deg, #6ed4ff 1.75%, #8fffa3 95.51%);
  background-repeat: no-repeat, no-repeat;
  background-size: cover, 100%;
  border-radius: 43px;

  @media (max-width: $table) {
    background-color: #8fffa3;
    background: linear-gradient(360deg, #6ed4ff 1.75%, #8fffa3 95.51%);
  }

  &::before {
    content: "";
    position: absolute;
    z-index: 2;
    left: 50%;
    top: get-vw(96px);
    transform: translateX(-50%);
    width: get-vw(125px);
    height: get-vw(125px);
    background-image: url("../img/icons/icon-shop.svg");
    background-repeat: no-repeat;
    background-size: contain;

    @media (max-width: $table) {
      display: none;
    }
  }

  &__header {
    padding: get-vw(75px);

    @media (max-width: $table) {
      width: 100%;
      text-align: center;
    }

    & h1 {
      margin-bottom: 25px;
      font-family: $mainFontBold;
      font-weight: $mainFontWeightBold;
      font-size: get-vw(52px);
      line-height: get-vw(63px);
      letter-spacing: 0.472727px;

      @media (max-width: $table) {
        font-size: get-vw(70px);
      }
    }

    & p {
      font-style: normal;
      font-weight: normal;
      font-size: get-vw(24px);
      line-height: get-vw(29px);
      letter-spacing: 0.48px;

      @media (max-width: $table) {
        font-size: get-vw(50px);
      }
    }
  }

  &__form {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    background: #ffffff;
    border-radius: 41px;
    padding: get-vw(75px);
    margin-top: 84px;

    @media (max-width: $table) {
      width: 100%;
    }

    &::before {
      content: "";
      position: absolute;
      z-index: 0;
      top: -84px;
      left: 0;
      width: 100%;
      height: 104px;
      background-color: #ffffff;
      opacity: 0.48;
      border-top-left-radius: 41px;
      border-top-right-radius: 41px;
    }

    &::after {
      content: "";
      position: absolute;
      z-index: 2;
      top: -44px;
      left: 0;
      width: 100%;
      height: 84px;
      background-color: #ffffff;
      opacity: 0.73;
      border-top-left-radius: 41px;
      border-top-right-radius: 41px;
    }
  }
}
